body {
  font-family: -apple-system, BlinkMacSystemFont, 'Expressway', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#header {
  border-bottom: rgb(201, 201, 201);
  border-bottom-style: solid;
  border-width: 0.5px;
}

#body {
  height: 93.9vh;
}

.full {
  height: 100%;
}

.row {
  margin-bottom: 5px;
}

#left {
  background-color: whitesmoke;
  max-width: 600px;
  min-width: 400px;
  padding-right: 30px;
  padding-left: 30px;
}

#position {
  background-color: lightgray;
  border-top: gray;
  border-bottom: gray;
  border-top-style: solid;
  border-bottom-style: solid;
  border-width: 0.5px;
  margin-right: -30px;
  margin-left: -30px;
}

.bold {
  font-weight: bold;
}

#navigationText {
  padding-left: 0px;
}

ol {
  margin-top: 10px;
  padding-left: 17px;
}

#right {
  padding-left: 0;
  padding-right: 0;
  height: 93vh;
  text-align: center;

  background-color: rgb(41, 41, 41);
}

canvas {
  height: 93vh;

  /* Have the canvas be centered when scaling */
  overflow: hidden;
  margin-left: 50%;
  transform: translateX(-50%);
}